import React ,{useEffect,useState}from 'react'

import db from './Firebase/config'

function List() {

    const [data,setData] = useState([])
    const [name,setName] = useState([])
    const [dta,setDta] = useState("")
    const [edit,setEdit] = useState("")
    const [ide,setIde] = useState("")
    const handleDelete = (id) =>{

        db.collection("users").doc(id).delete()

    }

    const handleChange = (e) =>{
        setName(e.target.value)

    }

    const handleSubmit = (e) =>{
        e.preventDefault()
        db.collection("users").add({
            name:name
        })
        setName("")
    }

    const handleEdit = (id) =>{
        db.collection('users').doc(id).get()
        .then(snapshot => setDta(snapshot.data()))
        setEdit(dta.name)
        setIde(id)

    }

    const handleUpdate = (e,ide) =>{
        e.preventDefault()

   
            db.collection("users").doc(ide).set({
            name:edit
        })
     


      
    }



    useEffect(()=>{
        loaddata();
  
    },[])


    const loaddata = () =>{
        db.collection("users").onSnapshot((snapshot)=>{
            const lists = []
            snapshot.forEach(doc=> lists.push(({...doc.data(),id:doc.id})))
            setData(lists);
          

        })
    }

       
          

            
    return (
        <div>
            {
                data.map((obj,k)=>{
                    return(
                        <h1>{obj.name} <br /> {obj.id} <input onClick={()=>handleDelete(obj.id)} type="button" value="Delete" />
                        <input onClick={()=>handleEdit(obj.id)} type="button" value="Update" />
                        </h1>
                    )
                })
            }



            <form>
  <div class="form-group">
    <label for="exampleInputEmail1">Email address</label>
    
    <input  value={name} onChange={handleChange} autoComplete='off' type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter name" />
  <br />
 <input onClick={handleSubmit} type="submit" class="btn btn-primary" value="submit"/><br />


  </div>

 
 
</form>
<br />
<form>
            <div class="form-group">
              <label for="exampleInputEmail1">Update address</label>
              
              <input  value={edit}  onChange={(e)=>setEdit(e.target.value)} autoComplete='off'   type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter name" required />
            <br />
           <input onClick={(e)=>handleUpdate(e,ide)} type="submit" class="btn btn-primary" value="submit"/><br />
          
          
            </div>
          
           
           
          </form>


        

    

 


        </div>
    )
}

export default List
